or-primary-3;\r\n        }\r\n    }\r\n    &__img-rotate {\r\n        width: 100%;\r\n        height: 210px;\r\n        background-size: cover;\r\n        background-position: 50% 50%;\r\n        background-repeat: no-repeat;\r\n        border-radius: 10px;\r\n        border-bottom-right-radius: 0;\r\n        border-bottom-left-radius: 0;\r\n        img {\r\n            width: 100%;\r\n            max-height: 210px;\r\n            border-radius: 10px;\r\n            border-bottom-right-radius: 0;\r\n            border-bottom-left-radius: 0;\r\n            object-fit: cover;\r\n        }\r\n    }\r\n    &__content {\r\n        margin-top: 20px;\r\n        padding: 0 25px;\r\n        background-color: #ffffff;\r\n        border-bottom-right-radius: 10px;\r\n        border-bottom-left-radius: 10px;\r\n        transition: $transition;\r\n        z-index: 2;\r\n    }\r\n    &__main-info {\r\n        display: flex;\r\n        margin-bottom: 20px;\r\n        flex-direction: column;\r\n        align-items: center;\r\n        .item__title {\r\n            width: 100%;\r\n            margin-bottom: 20px;\r\n            font-size: 25px;\r\n            font-weight: 800;\r\n            text-align: center;\r\n        }\r\n        .item__price {\r\n            margin: 0;\r\n            font-size: 22px;\r\n        }\r\n    }\r\n    &__button {\r\n        display: flex;\r\n        width: 100%;\r\n        max-width: 230px;\r\n        margin: 0 auto 45px;\r\n        padding: 10px;\r\n        justify-content: center;\r\n        align-items: center;\r\n        background-color: $color-primary-1;\r\n        color: rgba(255, 255, 255, .8);\r\n        border-radius: 100px;\r\n        transition: $transition;\r\n        &:hover {\r\n            box-shadow: 0 0 0 5px rgba(132, 138, 189, .3);\r\n            background-color: $color-primary-2;\r\n        }\r\n        .item__button-icon {\r\n            font-size: 30px;\r\n            margin-right: 10px;\r\n        }\r\n        .item__button-te